@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,400i,600,600i,700,700i);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,400i,600,600i,700,700i);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,400i,600,600i,700,700i);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,400i,600,600i,700,700i);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #545454; }

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%; }

.App-header {
  align-items: center;
  justify-content: center;
  width: 100%; }

.circle-row {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  width: 100%; }
  @media only screen and (max-width: 650px) {
    .circle-row .circle {
      width: 2em;
      height: 2em;
      border-radius: 1em;
      margin-top: 1em; } }
  @media only screen and (min-width: 650px) {
    .circle-row .circle {
      width: 5em;
      height: 5em;
      border-radius: 2.5em;
      margin-top: 1em; } }
  .circle-row .red {
    background: #ffa08f; }
  .circle-row .orange {
    background: #ffcb8f; }
  .circle-row .yellow {
    background: #fff08f; }
  .circle-row .green {
    background: #c4ff8f; }
  .circle-row .blue {
    background: #8fd7ff; }
  .circle-row .purple {
    background: #958fff; }
  .circle-row .pink {
    background: #ef8fff; }

.App-body {
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100%; }
  .App-body .bread-text {
    textAlignVertical: "center";
    textAlign: "center";
    font-size: 1.5em;
    font-family: "Josefin Sans", sans-serif;
    font-weight: 400; }
    .App-body .bread-text .bold {
      font-weight: 700; }

.background {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-width: 100%; }
  @media only screen and (max-width: 650px) {
    .background img {
      max-width: 100%; } }
  @media only screen and (min-width: 650px) {
    .background img {
      max-width: 70%; } }


